import React from 'react';
import VideoPlayer from "react-video-js-player";
import CANSAT from './videos/CANSAT.mp4';
import "./video.css";
const Video =()=> {
    const videoSrc= CANSAT;
    const poster="data:image/jpeg;base64,"
    return(
    <div className="App">
        
        <VideoPlayer src={videoSrc} poster={poster} width="580" height="400" playbackRates={[0.5, 1, 3.85,16]} />
    </div>
    );
};
export default Video;